    ul.subTokensList {
        padding: 0;
        border: 1px solid #eeeeee;
        li, li > a {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            text-decoration: none;
            color: $dark_grey;
            &.token_row_header {
                font-family: "Open_Sans-Bold";
                font-size: 1.05rem;
                color: white;
                background-color: $medium_blue;
            }
            &.token_item_row {  
                border-bottom: 1px inset $light_grey;      
                margin-bottom: -1px;  
                &:nth-child(odd) { background-color: var(--bs-gray-200);}                 
            }
            .token_column {
                border: none;
                padding: 0.5rem 1rem;
                &.token_name { width: 220px; min-width: 150px;}
                &.token_expired, &.token_revoked_time { width: 220px; min-width: 120px;}
                &.token_note, &.token_revoked_reason { width: 220px; min-width: 100px;}
                &.token_actions { 
                    width: 180px; min-width: 150px;
                    button { visibility: hidden;}
                }
                &.token_view {
                    width: 50px; height: 40px;
                    position: relative;
                }
                .view_icon {
                    position: absolute;
                    width: 25px; height: 25px;
                    background-color: $dark_grey;
                    opacity: 0;
                    @include bg_mask_image('../../../assets/icons/view_icon.svg');
                }
                
            }
            &:hover, &:focus {
                @include _transition(all, .2s, ease-out);
                .token_view .view_icon { opacity: 1;}
                .token_actions > button { visibility: visible;}
            }
        }
    }
}

.actions {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.token_btn {
    padding: 12px 20px;
    background-color: transparent;
    color: $dark_grey;
    position: relative;
    margin: 0 10px;
    font-size: 1rem;
    border: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
    .button_icon {
        width: 25px; height: 25px;
        margin-right: 8px;
        background-color: $dark_grey;
        &.edit_icon {
            @include bg_mask_image('../../../assets/icons/edit_icon.svg');
        }
        &.delete_icon {
            width: 28px; height: 28px;
            @include bg_mask_image('../../../assets/icons/shredder_icon.svg');
        }
    }
    &:hover, &:focus { 
        text-decoration: underline;
        .button_icon { background-color: $dark_grey;}
    }
    
}

/* CREATE TOKEN */
.form_indication_text {
    font-size: 0.8rem;
    color: red;
}
.create_token {
    /*
    @extend .token_btn;
    color: #666666;
