    width: 250px;
    height: 50px;
    text-align: center;
    font-size: 1.2rem;
    font-family: 'Open_Sans-Bold';
    text-transform: capitalize;
    @include border_radius(25px, 25px, 25px, 25px);
    color: black !important;
    //background-color: $yellow !important;
    background-image: linear-gradient($yellow, white);
    text-decoration: none;
    @include _transition(all, .4s, ease);
    //@include box_shadow(0 2px 8px 0, #888);
}
.cancel_btn {
    //background-color: var(--bs-gray-200);
    margin: 0 15px;
    color: var(--bs-gray-600);
}
.closeTokenForm_btn {
    width: 30px; height: 30px;
    background-color: transparent;
    border: none;
    position: relative;
    .close_icon {
        position: absolute;
        left: 0; top: 0;
        width: 30px; height: 30px;
        @include bg_mask_image('../../../assets/icons/close_cross_icon.svg');
        background-color: $dark_grey;
    }
}


.tokenForm_container {
    border: 1px solid #eeeeee;
    @include border_radius(3px, 3px, 3px, 3px);
    padding: 1rem 0.5rem;
    position: relative;
    @include _transition(height, 0.8s, ease-out);
}

#token_form {
    .token_input {
        position: relative;
        margin: 1rem 0;

        label {
            display: inline-block;
            margin-bottom: 10px;
            margin-right: 12px;
        }
        .range_value_wrap {
            text-align: right;
            margin-top: -8px;
        }
        &.token_date_input {
            display: flex;
            justify-content: flex-start;
            align-items: center;

            .creation_field {
                margin-right: 15px;
                width: 200px;
            }

            .expiration_field {
                width: 200px;

                .token_expiration {
                    padding: 0.375rem 0.75rem;
                    border: 1px solid #dddddd;
                    background-color: $light_grey;
                    @include border_radius(5px, 5px, 5px, 5px);
                }
            }

        }

    }


}

input.token_name_field {
    min-width: 300px;
    //     max-width: 500px;
    display: inline-block;
}

input.token_number_field {
    min-width: 50px;
    max-width: 100px;
}

