.datepicker_wrap {
    position: relative;

    .calendar_icon {
        content: "";
        @include _background(contain, 100% 100%);
        background-image: url("../../../assets/icons/calendar_date_icon.svg");
        width: 25px;
        height: 25px;
        position: absolute;
        top: 0.375rem;
        right: 0.75rem;

        &:hover {
            cursor: pointer;
        }
    }
}

/*
 * Darkmode
 */
.dark_bg {
    .closeTokenForm_btn .close_icon { background-color: $light_grey;}
    .create_token, .createToken_btn:hover, .createToken_btn:focus, .cancel_btn:hover, .cancel_btn:focus, .save_btn:hover, .save_btn:focus {
        background-color: $yellow;
        background-image: none;
        color: black;
    }
    .createToken_btn, .save_btn { color: black;}
    .back_btn { color: white;}
    .tokensList_container {
        table.tokensList {
            border-color: $dark_grey;
            thead th { background-color: $dark_grey;}
            tbody > tr {
                border-color: $dark_grey;
                &:nth-child(odd) > td { 
                    background-color: $light_grey; color: black;
                    .token_btn {
                        color: black;
                        .button_icon { background-color: black;}
                    }
                }
                &:nth-child(even) > td { 
                    background-color: #222222; color: white;
                    .token_btn {
                        color: white;
                        .button_icon { background-color: white;}
                    }
                }
            }
        }

    }
}